  width: 100vw;
  overflow: hidden;
  height: 100vh;
  color: #fff;
  background-color: #000;
  position: relative;
  background: url(~@/assets/daliuliang/bottom2.png) bottom/contain no-repeat, url(~@/assets/daliuliang/bg.png) center/cover no-repeat;
  .dialog {
    position: absolute;
    top: 184rem;
    z-index: 5;
    left: 50%;
    transform: translateX(-50%);
    width: 1093rem;
    height: 751rem;
    background: url(~@/assets/daliuliang/dialog.png) center/cover no-repeat;
    display: flex;
    flex-direction: column;
    align-items: center;
    .dialog_title {
      font-size: 42rem;
      font-family: newFont;
      padding: 6rem 20rem 26rem 0;
    }
    .picture {
      width: 867rem;
      height: 580rem;
      img {
        width: 100%;
      }
    }
    .close_btn {
      margin-top: 20rem;
      height: 45rem;
      line-height: 45rem;
      font-family: newFont;
      display: inline-block;
      text-align: center;
      cursor: pointer;
      display: flex;
      .btn_left {
        width: 35rem;
        height: 45rem;
        background: url(~@/assets/daliuliang/dialog_left.png) left center/35rem 45rem no-repeat;
      }
      .btn_center {
        font-size: 21rem;
        width: 80rem;
        height: 45rem;
        background: url(~@/assets/daliuliang/dialog_center.png) center/80rem 45rem;
      }
      .btn_right {
        width: 35rem;
        height: 45rem;
        background: url(~@/assets/daliuliang/dialog_right.png) right center/35rem 45rem no-repeat;
      }
    }
  }
  .map {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 1201rem;
    height: 1008rem;
    background: url(~@/assets/daliuliang/map.png) bottom center/1201rem 1008rem no-repeat;
    opacity: 0.8;
  }
  .top-bar {
    position: absolute;
    z-index: 3;
    font-family: "newFont";
    width: 100%;
    height: 112rem;
    text-align: center;
    line-height: 100rem;
    font-size: 40rem;
    background: url(~@/assets/daliuliang/top_bg.png) center top/1630rem no-repeat;
  }
  .bottom-bar {
    width: 100%;
    position: absolute;
    bottom: 0;
    background: url(~@/assets/daliuliang/bottom.png) center bottom/1778rem no-repeat;
    height: 63rem;
    text-align: center;
    img {
      width: 92rem;
      margin-top: 31rem;
      cursor: pointer;
    }
  }
  .contain {
    width: 100%;
    display: flex;
    margin-top: 112rem;
    // align-items: center;
    justify-content: space-between;
  }
  .left-box {
    margin-left: 35rem;
    transform: translateY(-70rem);
  }
  .right-box {
    transform: translateY(-70rem);
    display: flex;
    flex-direction: column;
    .radar-chart {
      width: 350rem;
      height: 190rem;
    }
    .bar-chart {
      width: 350rem;
      height: 180rem;
    }
    .stop_car {
      font-size: 20rem;
      font-family: newFont;
      .label_title {
        margin-top: 10rem;
      }
      .stop_card {
        display: inline-block;
        font-size: 16rem;
        width: 165rem;
        height: 55rem;
        background: url(~@/assets/daliuliang/stop_card1.svg) center/contain no-repeat, url(~@/assets/daliuliang/stop_card2.svg) left/69rem 64rem no-repeat;
        .label_one {
          text-align: right;
          padding: 5rem 10rem 0 0;
        }
        .label_two {
          font-size: 18rem;
          text-align: right;
          padding: 0 10rem 0 0;
        }
      }
    }
  }
  .cards {
    display: flex;
    flex-wrap: wrap;
    width: 350rem;
    // height: 100rem;
    .card_small {
      opacity: 0.8;
      transform: translateY(7rem);
      width: 163rem;
      height: 55rem;
      padding: 5rem 0 0 20rem;
      background: url(~@/assets/daliuliang/card_small.png) center/163rem 55rem no-repeat;
      .row-one {
        font-size: 12rem;
      }
      .row-two {
        font-family: newFont;
        font-size: 22rem;
      }
    }
    .card_big {
      opacity: 0.8;
      padding: 5rem 0 0 20rem;
      width: 163rem;
      height: 95rem;
      background: url(~@/assets/daliuliang/card_big.png) center/163rem 105rem no-repeat;
      .row-one {
        font-size: 12rem;
      }
      .row-two {
        font-family: newFont;
        font-size: 22rem;
      }
      .row-three {
        font-size: 12rem;
        .row-three-one,
        .row-three-two {
          display: flex;
          align-items: center;
        }
        .icon1_img {
          display: inline-block;
          width: 14rem;
          height: 14rem;
          background: url(~@/assets/daliuliang/icon1.png) center/25rem no-repeat;
        }
        .red-num {
          color: #e57800;
        }
        .blue-num {
          color: #00a3e5;
        }
      }
    }
  }
  .title_oneself {
    font-family: newFont;
    font-size: 22rem;
    padding-left: 8rem;
  }
  .line-charts1,
  .line-charts2,
  .line-charts3 {
    width: 320rem;
    height: 120rem;
    margin-bottom: 10rem;
  }
  .table_move {
    width: 350rem;
    padding-right: 26rem;
    .table_one {
      margin: 10rem 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .btns {
        display: flex;
        .btn1,
        .btn2 {
          margin-right: 10rem;
          width: 56rem;
          height: 25rem;
          line-height: 25rem;
          text-align: center;
          cursor: pointer;
          background: url(~@/assets/daliuliang/btn1.png) center/contain no-repeat;
        }
        .isAcitve {
          background: url(~@/assets/daliuliang/btn2.png) center/contain no-repeat;
        }
      }
      .migration {
        padding: 5rem 15rem;
        background-color: rgba(18, 137, 221, 0.3);
        span {
          font-family: newFont;
          padding-left: 5rem;
        }
      }
    }
    .table_two {
      overflow: hidden;
      height: 180rem;
      .table_th {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 30rem;
        & > div {
          text-align: center;
        }
        .th_one {
          width: 50rem;
        }
        .th_two {
          width: 50rem;
          text-align: left;
        }
      }
      .roll-box {
        position: relative;
        overflow: hidden;
        height: 150rem;
        .table_td {
          display: flex;
          align-items: center;
          justify-content: space-between;
          height: 30rem;

          & > div {
            text-align: center;
          }
          .td_one {
            display: flex;
            align-items: center;
            .table_icon {
              display: inline-block;
              background: url(~@/assets/daliuliang/table-icon.png) center/30rem no-repeat;
              width: 30rem;
              height: 30rem;
            }
          }
          .td_two {
            width: 100rem;
            margin-left: 37rem;
            text-align: left;
          }
        }
        .table_td:nth-of-type(even) {
          background: rgba(25, 214, 255, 0.1);
        }
        #box1 {
          position: absolute;
          width: 100%;
        }
        .animation-stop {
          animation-play-state: paused !important;
        }
        .animation-start {
          animation-play-state: running !important;
        }
      }
    }
  }
  .alarm-warning {
    padding-left: 50rem;
    padding-bottom: 6rem;
    position: absolute;
    z-index: 2;
    top: 135rem;
    left: 50%;
    width: 721rem;
    height: 97rem;
    transform: translateX(-50%);
    font-size: 25rem;
    line-height: 25rem;
    background: url(~@/assets/daliuliang/alarm.png) center/721rem 97rem no-repeat;
    display: flex;
    align-items: center;
    .carousel-container {
      overflow: hidden;
      height: 50rem;
    }

    #carousel-list {
      list-style: none;
      margin: 0;
      padding: 0;
      position: relative;
    }

    #carousel-list li {
      // height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .alarm-content {
      display: flex;
      align-items: center;
    }
    img {
      transform: translateY(2rem);
    }
    .alarm_btn {
      margin-left: 10rem;
      font-family: newFont;
      font-size: 18rem;
      width: 81rem;
      height: 34rem;
      line-height: 35rem;
      display: inline-block;
      text-align: center;
      background: url(~@/assets/daliuliang/alarm-btn.png) center/81rem 56rem no-repeat;
      cursor: pointer;
    }
  }
  .tooltip_item {
    position: absolute;
    z-index: 2;
    left: 1269rem;
    top: 579rem;
    width: 260rem;
    height: 150rem;
    background: url(~@/assets/daliuliang/alarm_tooltip.png) top right/252rem 140rem no-repeat, url(~@/assets/daliuliang/alarm_tooltip.png) top right/252rem 140rem no-repeat,
      url(~@/assets/daliuliang/alarm_tooltip.png) top right/252rem 140rem no-repeat, url(~@/assets/daliuliang/map_icon1.png) bottom left/30rem no-repeat,
      url(~@/assets/daliuliang/map_icon2.png) bottom left/30rem no-repeat;
    padding: 15rem 0 0 40rem;
    .icon-row {
      display: flex;
      align-items: center;

      .icon_alarm {
        width: 30rem;
        height: 30rem;
        background: url(~@/assets/daliuliang/warning_icon2.svg) center/40rem no-repeat;
      }
      span {
        font-size: 18rem;
      }
    }
    .row-two {
      font-size: 22rem;
      font-weight: 600;
      color: #ff9c73;
    }
    .row-three,
    .row-four {
      font-size: 12rem;
    }
  }
}
@font-face {
  font-family: "newFont";
  src: url("./assets/daliuliang/newFont.ttf");
}
@keyframes movetop1 {
  from {
    top: 0;
  }
  to {
    top: 0;
  }
}
